angular.module("MyApp", ["PageM", "FilterM","ToolM", "cfp.hotkeys"])
         .controller("MyController", ["$scope", "PageProvider","ToolProvider", 'hotkeys', function ($scope, PageProvider,TPM, hotkeys) {

             $scope.PM = PageProvider;
             $scope.TM = {};
             //$scope.TM.isHide = true;
             $scope.VM = {};
             $scope.VM.locationObj = {};
             $scope.VM.locationObj.text = "";
             $scope.VM.locationObj.id = 0;
             $scope.VM.title = "";
             $scope.VM.publishState = "0";

             //$("#tmBlock").show();
             //$scope.PM.changePage("", tbData, function (data) {
             //    $scope.TM.data = data.message;
             //    history.replaceState({ "pageIndex": 1 }, document.title, location.href);
             //});
             //$scope.changePage(1);

             $scope.changePage = function (pageIndex, noHistory) {
                 $("#tmBlock").show();
                 var pgData = $scope.PM.genPageData(pageIndex);
                 var postData = {};
                 postData["s"] = "";
                 postData["e"] = "";
                 //postData.LID = $scope.VM.locationObj.id;
                 postData["lid"] = window.getOrgID();
                 if (window.getOrgName() == "") {
                     postData["oid"] = 0;
                 }
                 postData["s"] = $("#tbDateStart").val().replace(/年/g, "");
                 postData["e"] = $("#tbDateEnd").val().replace(/年/g, "");
                 postData = $scope.PM.filterPageData(pgData, postData);
                 $scope.PM.changePage("/Vaccination/RecordM/BCGRecordOP.aspx", postData, function (data) {
                    
                     $scope.TM.data = data.message;
                     $scope.$apply(function () { });

                     //if (history.pushState && noHistory == undefined) {
                     //    if (pageIndex > 1 || location.hash.length > 0) {
                     //        history.pushState({ "pageIndex": pageIndex }, document.title, location.href.split("?")[0] + "?#h");
                     //    }
                     //    else
                     //    {
                         
                     //        history.replaceState({ "pageIndex": 1 }, document.title, location.href);
                     //    }
                     //}
                 });
             };

             hotkeys.add({
                 combo: 'enter',
                 description: 'Description goes here',
                 allowIn: ['INPUT', "BUTTON", 'SELECT', 'TEXTAREA'],
                 callback: function (event, hotkey) {
                     var focusType = document.activeElement.type;
                     if (focusType == "text" || focusType == "select-one") {
                         $scope.changePage(1);
                     }
                 }
             });

             //$scope.openOrgs = function (record) {
             //    TPM.popUpWindow("/SelectSingleOrg.aspx", "SelectSingleOrg", 620, 450);
             //};
             
             $scope.goDetail = function (record) {
                 $scope.save();
                 var obj = {};
                 obj['i'] = record["I"];
                 TPM.doPOST("/Vaccination/RecordM/BCGRecord_Detail.aspx", obj,document.querySelector('#MyForm'));
             };

             $scope.save = function () {
                 var data = {};
                 data["OrgID"] = window.getOrgID();
                 data["OrgName"] = window.getOrgName();
                 data["tbDateStart"] = $("#tbDateStart").val().replace(/年/g, "");
                 data["tbDateEnd"] = $("#tbDateEnd").val().replace(/年/g, "");
                 data['pgNow'] = $scope.PM.pgNow;
                 TPM.setData(data);
                 return JSON.stringify(data);
             };

             $scope.doReset = function () {
                 $("#tbDateStart").val("");
                 $("#tbDateEnd").val("");
                 $("#tmBlock").hide();
                 $scope.PM.reset();
                 window.setOrgID(defaultOrgID);
                 window.setOrgName(defaultOrgName);
             };

             if (location.hash.length > 0) {
                 var data = TPM.getData();
                 $("#ucOrgID").val(data.OrgID);
                 $("#ucOrgName").val(data.OrgName);
                 $("#tbDateStart").val(data.tbDateStart + "年");
                 $("#tbDateEnd").val(data.tbDateEnd + "年");
                 $scope.changePage(data.pgNow);
             }
             else {
                 $scope.doReset();
             }

             $scope.getYearSeason = function (record) {
               
                 switch (record["YS"])
                 {
                     case 1:
                         return "第一季";
                         break;
                     case 2:
                         return "第二季";
                         break;
                     case 3:
                         return "第三季";
                         break;
                     case 4:
                         return "第四季";
                         break;

                 }
             };
             

}]);

